.tab{
  font-weight:300;
  font-size: 16px;
  line-height: 2;
}

.title {
  font-size: 25px;
  color: darkgrey;
  line-height: 1;
  margin-bottom: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  /* transition: all .2s; */
}

.gifs {
  opacity: .5;
  border-radius: 5px;
}

.gifs:hover {
  opacity: 1;
}

.title:hover {
  text-decoration: none;
}

.detail {
  padding: 20px;
  height: 100%;
  overflow-y: scroll;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.3)), to(transparent));
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0.3), transparent);
  background: -o-linear-gradient(rgba(255, 255, 255, 0.3), transparent);
  background: linear-gradient(rgba(255, 255, 255, 0.3), transparent);
}



