.dropdown-content {
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    -ms-flex-pack: justify;
        justify-content: space-between;
    min-width: 125px;
    /* z-index: 1; */
    /* animation: .5s detailfadeout forwards; */
    margin-top: 32px;
    /* padding-top: -50px; */
    opacity: 0;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    visibility: hidden;
}

.dropdown-content img:hover {
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
  opacity: 1;
  /* transition: all 1s ease-in-out; */
}

.dropdown-content img {
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  opacity: .6;
}

@-webkit-keyframes fadein {
  0% {
    visibility: hidden;
    margin-top: -5px;
  }
  50% {
    visibility: hidden;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    visibility: hidden;
    margin-top: -5px;
  }
  50% {
    visibility: hidden;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes slidein {
  0% {
    opacity: 0;
    margin-right: 300px;
  }
  100% {
    margin-right: 0px;
    opacity: 1;
  }
}

@keyframes slidein {
  0% {
    opacity: 0;
    margin-right: 300px;
  }
  100% {
    margin-right: 0px;
    opacity: 1;
  }
}

@font-face {
	font-family: 'HelveticaNeue';
	src: url('HelveticaNeue.ttc');
}



